@import url("https://fonts.googleapis.com/css2?family=Fira+Sans:wght@400;900&display=swap");

.menu {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 2vh;
}

.menuItem {
  font-family: "Fira Sans", "Times New Roman";
  height: 3em;
  width: 11em;
  cursor: pointer;
  background-color: white;
  border-color: gray;
  margin: 2px;
  border-style: solid;
  border-width: 1px;
  border-color: black;
  color: black;
  text-align: center;
}

.menuItem:hover {
  box-shadow: 0px 0px 2px 2px lightblue;
}

.menuItem:active {
  box-shadow: 0px 0px 3px 3px lightblue;
}

a {
  text-decoration: none;
}
