@import url("https://fonts.googleapis.com/css2?family=Fira+Sans:wght@400;600;900&display=swap");

/* Start CSS reset code from lecture */

*,
*::before,
*::after {
  box-sizing: border-box;
}

* {
  margin: 0;
  font-family: "Fira Sans", "Times New Roman";
}

img,
picture,
video,
canvas,
svg {
  display: block;
  max-width: 100%;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

a {
  color: blue;
  text-decoration: underline;
}

/* End CSS reset code from lecture */

body {
  overflow-y: scroll;
  overflow-x: hidden;
  line-height: 1.5;
}

@media (min-width: 501px) {
  h1 {
    font-size: 40px;
  }

  h2 {
    margin-bottom: 0.5em;
    font-size: 20px;
  }

  h3 {
    font-size: 18px;
  }

  h4 {
    font-size: 15px;
  }

  p,
  li {
    font-size: 18px;
  }

  button {
    font-size: 15px;
  }
}

@media (max-width: 500px) {
  h1 {
    font-size: 40px;
  }

  h2 {
    margin-bottom: 0.5em;
    font-size: 20px;
  }

  h3 {
    font-size: 15px;
  }

  h4 {
    font-size: 12px;
  }

  p,
  li {
    font-size: 15px;
  }

  button {
    font-size: 15px;
  }
}

#App {
  background-color: white;
  height: 100vh;
  width: 100vw;
  display: block;
}

#App-noScroll {
  background-color: white;
  height: 100vh;
  width: 100vw;
  display: block;
}

#appHeaderContainer {
  display: flex;
  display: space-between;
  align-items: center;
}

#title {
  color: black;
  font-weight: 900;
  font-family: "Fira Sans", "Times New Roman";
