  text-align: center;
  font-size: 5vw;
  margin-top: 2vh;
  width: 65vw;
  min-height: 40px;
  margin-left: auto;
  margin-right: auto;
}

#line {
  width: 75vw;
  margin: auto;
  height: 9vw;
